import { ViewChild, ElementRef, AfterViewInit, Component, Input } from '@angular/core';
declare var $: any;
@Component({
  selector: 'app-carousel',
  template: `<div #carousel>
               <div *ngFor="let photo of photos">
                  <img [src]="photo">
              </div>
            </div> 
            <div #carouselNav>
            </div>
            `,
  styleUrls: ['./carousel.component.css']
})
export class CarouselComponent implements AfterViewInit {
  @ViewChild('carousel') carousel: ElementRef;
  @ViewChild('carouselNav') carouselNav: ElementRef;
  @Input() photos: string[];

  constructor() { }

  ngAfterViewInit() {

     $(this.carousel.nativeElement).slick({
        slidesToShow: 1,
        slidesToScroll: 1,
        arrows: false,
        fade: true,
        asNavFor: '.slider-nav'
      });
      $(this.carouselNav.nativeElement).slick({
        slidesToShow: 3,
        slidesToScroll: 1,
        asNavFor: '.slider-for',
        dots: true,
        centerMode: true,
        focusOnSelect: true
      });
  }

}



